// Startscreen, beim aufrufen der erscheint das Pop-up (showPopup)
// nachdem Spiel starten geklickt wird, wird Pop-up geschlossen und GameScreen wird angezeigt.
import React, { useState, useEffect } from 'react';
import GameScreen from './GameScreen';
import './GameScreen.css';
import './App.css';
import FinalScreen from './FinalScreen';


function App() {

const initialBackgroundImage = require('./pictures/eingang.jpeg');
const [showPopup, setShowPopup] = useState(true);
const [showFinalScreen, setShowFinalScreen] = useState(false);
const [totalPoints, setTotalPoints] = useState(0);

    const closePopup = () => {
    setShowPopup(false);
  };

  const handleGameCompletion = (points) => {
    setTotalPoints(points);
    setShowFinalScreen(true);
  };

  useEffect(() => {
    document.body.style.backgroundImage = `url(${initialBackgroundImage})`;
    document.body.style.backgroundSize = '100% 100%';
    document.body.style.backgroundRepeat = 'no-repeat';
    document.body.style.backgroundPosition = 'center center';
  }, []);
  
  

  return (
    <div className="App" style={{ backgroundColor: 'transparent' }}>
      {showPopup ? (
        <div className="popup-container">
          <div className="popup">
            <div className="popup-content">
            <h2 id='heading'>Willkommen zum ASCII und Unicode Lernspiel</h2>
              <p id='text'>Schaffst du es alle Rtsel zu lsen um aus dem Tempel zu entkommen?
              Auf deinem Weg nach drauen musst du 10 verschiedene Rume durchqueren.
              In jedem Raum warten drei unterschiedliche Aufgaben auf dich, die du lsen musst, 
              um den Weg nach drauen zu finden.
              Jeder einzelne Raum wird durch ein Level in diesem Spiel reprsentiert.
              Mit jeder korrekten Antwort sammelst du Punkte fr dein Punktekonto. Aber Vorsicht!
              Sei nicht zu schnell. Bei den Aufgaben, bei denen dir Antwortmglichkeiten vorgegeben werden,
              knnen dir Punkte abgezogen werden. Das Raten von Antworten kann deinen Punktestand schnell schmelzen lassen.
              Fr deinen Weg nach drauen gebe ich dir zwei Schriftrollen mit, die dir behilflich sein knnten. 
              Bist du dir bei einem Rtsel unsicher, empfehle ich dir dort reinzuschauen. Im Spiel sind sie als Erklrungen gekennzeichnet. 
              An der Leiste, die du oben sehen kannst, kannst du erkennen auf welchem Level du dich befindest und wie viele
              du noch vor dir hast. Du kannst dich in dem Tempel zwar frei bewegen und dich in den einzelnen Rumen umsehen,
              aber die letzte Tr ffnet sich nur, wenn alle anderen Tren im Tempel bereits geffnet worden sind. Viel Erfolg!</p>
              <button className='button' onClick={closePopup}>Spiel Starten</button>
            </div>
          </div>
        </div>
      ) : (
        <header className="App-header" style={{ backgroundColor: 'transparent' }}>
          <div>
          </div>
          {showFinalScreen ? (
        <FinalScreen totalPoints={totalPoints} />
      ) : (
        <GameScreen onGameCompletion={handleGameCompletion} />
      )}
        </header>
      )}
    </div>
  );
 }
  

export default App;